html {
  color: #060b35;
  font-family: Roboto, sans-serif;
}

body, p {
  margin: 0;
}

.header {
  justify-content: space-between;
  height: 60px;
  padding: 0 50px;
  line-height: 60px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  margin: 10px 0;
}

.nav {
  font-size: 12px;
  font-weight: 500;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  display: block;
  position: relative;
  top: -4px;
  left: 0;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 300px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  text-align: center;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card:hover {
  color: #34568b;
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 36px;
}

.card__info {
  text-align: left;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
